<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card>
        <v-card-title class="headline font-weight-regular blue-grey white--text">Provider</v-card-title>
        <v-card-text>
          <v-form ref="form">
            <v-container fluid>
              <v-subheader>Audit Details</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit[0].id" label="Audit ID" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit[0].auditor" label="Auditor" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit[0].audit_date" label="Audit Date" disabled></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit[0].provider_name" label="MD Name" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field v-model="audit[0].npi" label="MD NPI" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md2>
                  <v-text-field v-model="audit[0].entity_name" label="Facility" disabled></v-text-field>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field v-model="audit[0].audit_type" label="Audit Type"></v-text-field>
                </v-flex>
              </v-layout>
              <v-subheader>Audit Results</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-checkbox v-model="audit[0].no_charges_found" label="No Charges Found"></v-checkbox>
                </v-flex>
                <v-flex xs12 md6>
                  <v-checkbox
                    v-model="audit[0].manually_selected_charges"
                    label="Manually Selected Charges"
                  ></v-checkbox>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerChargeCorrection"
                    :close-on-content-click="false"
                    v-model="menuDatePicker"
                    :nudge-right="40"
                    :return-value.sync="audit[0].charge_correction_start_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].charge_correction_start_date"
                      label="Charge Correction Start Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Charge Correction Start Date"
                      :error-messages="errors.collect('Charge Correction Start Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].charge_correction_start_date"
                      @input="$refs.menuDatePickerChargeCorrection.save(audit[0].charge_correction_start_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field
                    v-model="audit[0].audit_results_communicated_date"
                    label="Audit Results Communicated Date"
                    disabled
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 md6>
                  <v-menu
                    ref="menuDatePickerAuditComplete"
                    :close-on-content-click="false"
                    v-model="menuDatePicker"
                    :nudge-right="40"
                    :return-value.sync="audit[0].charge_correction_start_date"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="audit[0].audit_completed_date"
                      label="Audit Completed Date"
                      prepend-icon="event"
                      readonly
                      data-vv-name="Audit Completed Date"
                      :error-messages="errors.collect('Audit Completed Date')"
                    ></v-text-field>
                    <v-date-picker
                      v-model="audit[0].audit_completed_date"
                      @input="$refs.menuDatePickerAuditComplete.save(audit[0].audit_completed_date)"
                    ></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs12 md6>
                  <v-text-field v-model="audit[0].score" label="Audit Score" disabled></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex>
                  <v-btn>Save</v-btn>
                  <v-btn>Cancel</v-btn>
                </v-flex>
              </v-layout>
              <v-subheader>Encounters</v-subheader>
              <v-divider></v-divider>
              <v-layout row wrap>
                <v-flex xs12>
                  <v-list>
                    <v-list-tile-title>
                      <v-layout>
                        <v-flex md4>Patient Name</v-flex>
                        <v-flex md2>CPT</v-flex>
                        <v-flex md2>Date of Service</v-flex>
                        <v-flex md2>Status</v-flex>
                        <v-flex md2></v-flex>
                      </v-layout>
                    </v-list-tile-title>
                    <template v-for="(encounter, index) in audit">
                      <v-list-tile :key="index" @click="auditEncounter(encounter.audit_record_id)">
                        <v-layout>
                          <v-flex md4>{{ encounter.patient_name }}</v-flex>
                          <v-flex md2>{{ encounter.cpt_billed }}</v-flex>
                          <v-flex md2>{{ encounter.service_date }}</v-flex>
                          <v-flex
                            md2
                            v-if="encounter.pass === true || encounter.pass === false"
                          >Complete</v-flex>
                          <v-flex md2 v-else>Pending</v-flex>
                          <v-flex md2>
                            <v-btn small style="margin:0;height:20px;">Delete</v-btn>
                          </v-flex>
                        </v-layout>
                      </v-list-tile>
                    </template>
                  </v-list>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      title="Confirm"
      message="Are you sure you want to cancel your changes?"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import httpService from "../httpService";
import Router from "../router";

// audit_date: new Date().toJSON()
export default {
  name: "Main",
  components: {
    ConfirmationDialog,
    CustomSnackbar
  },
  data: () => ({
    menuDatePicker: null,
    menuDatePickerChargeCorrection: null,
    menuDatePickerAuditResults: null,
    menuDatePickerAuditComplete: null,
    audit: {
      audit_date: "",
      comments: "",
      auditFailReason: "",
      auditFailReasonOther: "",
      pass: ""
    }
  }),
  mounted() {
    // work on this one record
    //this.audit = this.audits.find(elem => elem.audit_id == this.$route.params.audit_id);
    this.init();
  },
  computed: {
    newRecord() {
      return false;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    auditTypes() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        row => row.keyword === "AUDIT_TYPE"
      ).map(row => row.value);
    },
    failReasons() {
      return this.$store.getters.APPLICATION_LISTS.filter(
        row => row.keyword === "AUDIT_FAIL_REASON"
      )
        .map(row => row.value)
        .sort();
    },
    currentPhysicianName() {
      if (this.audit.npi) {
        const indx = this.physicians.findIndex(
          obj => obj.npi === this.audit.npi
        );
        return (
          this.physicians[indx].last_name +
          ", " +
          this.physicians[indx].first_name
        );
      }
    }
    // },
    // auditorName() {
    //   if (this.audit.auditor_id >= 0) {
    //     const indx = this.auditors.findIndex(
    //       obj => obj.id === this.audit.auditor_id
    //     );
    //     return this.auditors[indx].fullname;
    //   }
    // }
  },
  methods: {
    async init() {
      httpService.get(
        `${Config.apiRoot}/audit/physician/audit/${this.$route.params.npi}/${
          this.$route.params.provider_audit_id
        }`,
        (status, data) => {
          this.audit = data.message;
        }
      );
    },
    auditEncounter: function(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    },
    removeChip(obj, item) {
      obj.splice(obj.indexOf(item), 1);
      obj = [...obj];
    },
    saveAudit: function() {
      this.$validator.validateAll().then(async result => {
        if (!result) {
          return;
        } else {
          httpService.put(
            `${Config.apiRoot}/audit/${this.audit.id}`,
            this.audit,
            (status, data) => {
              if (data.success) {
                this.showMessage("success", "Record sucessfully saved");
              } else {
                this.showMessage(
                  "error",
                  "There was a problem saving your changes.  MSG - " +
                    data.message.originalError.info.message
                );
              }
            }
          );
        }
      });
    },
    cancelAudit: function() {
      this.$refs.confirmationDialog.confirmation().then(res => {
        if (res) {
          this.$router.go(-1);
        }
      });
    },
    testPassFail: function(index) {
      if (this.audit.details[index].pass) {
        this.audit.details[index].auditFailReason = "";
        this.audit.details[index].auditFailReasonOther = "";
      } else if (
        !this.audit.details[index].pass &&
        (!this.audit.details[index].cpt_audited ||
          this.audit.details[index].cpt_audited === "")
      ) {
        this.audit.details[index].auditFailReason = "Non-Billable";
      } else {
        this.audit.details[index].auditFailReason = "";
      }
    },
    aatestFailReason: function(index) {
      // TODO - not working correctly.  causes field to not be updated for a couple of clicks on fields.
      if (this.audit.details[index].auditFailReason != "Other") {
        this.audit.details[index].auditFailReasonOther = "";
      }
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, true);
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}
.v-subheader-donotuse {
  background-color: lightblue;
}
</style>
