<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title
          class="headline font-weight-regular blue-grey white--text"
        >Create Audit for Practice</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md2 pr-1>
              <v-select
                v-model="selectedEntityId"
                :items="entities"
                item-text="entity_name"
                item-value="id"
                label="Entity"
              ></v-select>
            </v-flex>
            <v-flex xs12 md6 pr-2>
              <v-autocomplete
                v-model="selectedPractice"
                :items="filteredPractices"
                persistent-hint
                item-text="group_practice"
                item-value="id"
                label="Practice"
              ></v-autocomplete>
            </v-flex>
            <v-flex xs12 md2 pr-1>
              <v-select v-model="auditMonth" :items="months" label="Audit Month"></v-select>
            </v-flex>
            <v-flex xs12 md2>
              <v-select v-model="auditYear" :items="years" label="Audit Year"></v-select>
              <!--              <v-select
                v-model="selectedPracticeAuditId"
                :items="practiceAudits"
                label="Open Audits"
                item-value="id"
                item-text="created_date"
              ></v-select>
              -->
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12>
              <v-checkbox v-model="allProviders" label="Create Audit for All Providers?"></v-checkbox>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md4 offset-md4>
              <v-btn block color="blue" @click="startPracticeAudit()">Start New Audit for Practice</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>

      <v-card class="mb-2" v-if="false">
        <!--  v-if="selectedPractice && selectedPracticeAuditId" -->
        <v-card-title class="headline font-weight-regular blue-grey white--text">Provider Selection</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md6 pr-2>
              <v-autocomplete
                v-model="selectedNpi"
                :items="physicianLookup"
                persistent-hint
                item-text="name"
                item-value="npi"
                label="Provider"
              ></v-autocomplete>
            </v-flex>
            <v-flex xs12 md6>
              <v-select
                v-model="selectedPhysicianAuditId"
                :items="physicianAudits"
                label="Open Provider Audits"
                item-value="id"
                item-text="created_date"
              ></v-select>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md4 offset-md4>
              <v-btn block color="blue" @click="startPhysicianAudit()">Start New Audit for Provider</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>

      <v-card class="mb-2" v-if="selectedNpi && selectedPhysicianAuditId">
        <v-card-title class="headline font-weight-regular blue-grey white--text">Patient Selection</v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 md6>
              <v-autocomplete
                v-model="selectedNpi"
                :items="physicianLookup"
                persistent-hint
                item-text="name"
                item-value="npi"
                label="Physician"
              ></v-autocomplete>
            </v-flex>
            <v-flex xs12 md3>
              <v-menu
                :close-on-content-click="false"
                v-model="menu1"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                max-width="290px"
                min-width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="auditBeginDate"
                  label="Begin Date"
                  persistent-hint
                  prepend-icon="event"
                  readonly
                ></v-text-field>
                <v-date-picker v-model="auditBeginDate" no-title @input="menu1 = false"></v-date-picker>
              </v-menu>
            </v-flex>
            <v-flex xs12 md3>
              <v-menu
                :close-on-content-click="false"
                v-model="menu2"
                :nudge-right="40"
                lazy
                transition="scale-transition"
                offset-y
                full-width
                max-width="290px"
                min-width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="auditEndDate"
                  label="End Date"
                  persistent-hint
                  prepend-icon="event"
                  readonly
                ></v-text-field>
                <v-date-picker v-model="auditEndDate" no-title @input="menu2 = false"></v-date-picker>
              </v-menu>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md6>
              <v-autocomplete
                v-model="auditPayer"
                :items="payerListing"
                persistent-hint
                item-text="original_payor"
                label="Payer"
                multiple
              ></v-autocomplete>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12>
              <v-btn block color="blue" @click="searchProcedures()">Search</v-btn>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>

      <v-layout row wrap v-if="initialSearch">
        <v-flex xs12 md7 class="pr-1">
          <v-card class="mb-2">
            <v-card-title
              class="headline font-weight-regular blue-grey white--text"
            >Procedure Summary</v-card-title>
            <v-card-text>
              <v-list>
                <v-list-tile
                  v-if="this.distinctProcedures.length === 0 && initialSearch"
                >No Data Found</v-list-tile>
                <v-list-group
                  v-for="procedure in this.distinctProcedures"
                  :key="procedure"
                  no-action
                >
                  <v-list-tile slot="activator">
                    <v-list-tile-content>
                      <v-list-tile-title>{{ procedure }} ({{ listGroupCountForProcedures(procedure) }})</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-list-tile
                    class="record-selector"
                    v-for="(item, index) in procedureListing"
                    :key="index"
                    v-if="item.cpt === procedure && item.visible != false"
                    @click="addPatientToList(item.encounter)"
                  >
                    <v-list-tile-content>
                      <v-list-tile-title>{{ item.patient_name }}</v-list-tile-title>
                      <v-list-tile-sub-title>
                        <v-layout row wrap>
                          <v-flex
                            xs12
                            lg6
                          >CPT: {{ patientCptList(item.encounter).toString().replace(/[\"]/, "") }}</v-flex>
                          <v-flex xs12 lg6>Service Date: {{ item.service_date }}</v-flex>
                        </v-layout>
                        <v-layout row wrap>
                          <v-flex xs12 lg6>Location: {{ item.location }}</v-flex>
                          <v-flex xs12 lg6>Diagnosis Codes: {{ item.icd10_billed }}</v-flex>
                        </v-layout>
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-icon color="grey darken-1">arrow_forward</v-icon>
                    </v-list-tile-action>
                  </v-list-tile>
                </v-list-group>
              </v-list>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs21 md5>
          <v-card class="mb-2">
            <v-card-title class="headline font-weight-regular blue-grey white--text">Selected</v-card-title>
            <v-card-text>
              <v-list>
                <v-list-tile
                  v-if="selectedEncounters.length === 0 && initialSearch"
                >No Records Selected</v-list-tile>
                <v-list-tile
                  class="record-selector"
                  v-for="(sel, index) in selectedEncounters"
                  :key="index"
                  @click="popPatientFromList(sel)"
                >
                  <v-list-tile-action>
                    <v-icon>arrow_back</v-icon>
                  </v-list-tile-action>
                  <v-list-tile-content>
                    <v-list-tile-title>{{ patientFromEncounter(sel).patient_name }}</v-list-tile-title>
                    <v-list-tile-sub-title>
                      <v-layout row wrap>
                        <v-flex
                          xs12
                          lg6
                        >CPT: {{ patientFromEncounter(sel).cpt.toString().replace(/[\"]/, "") }}</v-flex>
                        <v-flex xs12 lg6>Service Date: {{ patientFromEncounter(sel).service_date }}</v-flex>
                      </v-layout>
                    </v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list>
            </v-card-text>
            <div class="text-xs-center">
              <v-btn v-if="selectedEncounters.length" @click="createAudits()">Create Audits</v-btn>
            </div>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import CustomSnackbar from "@/components/Snackbar";
import Config from "../config";
import httpService from "../httpService";

// auditBeginDate: "2017-12-01",
// auditEndDate: "2018-11-15",
// auditPayer: "MEDICARE",
// selectedNpi: "1245218619",
// auditBeginDate: null,
// auditEndDate: null,
// auditPayer: null,
// selectedNpi: null,

export default {
  name: "Main",
  components: { CustomSnackbar },
  data: () => ({
    allProviders: false,
    selectedEntityId: null,
    selectedPractice: null,
    selectedPracticeAuditId: null,
    selectedPhysicianAuditId: null,
    physicianAudits: [],
    practiceAudits: [],
    payerListing: [],
    procedureListing: [],
    selectedEncounters: [],
    menu1: false,
    menu2: false,
    initialSearch: false,
    auditMonth: null,
    auditYear: null,
    auditBeginDate: "2017-12-01",
    auditEndDate: "2018-11-15",
    auditPayer: null,
    selectedNpi: "1790762623",
    months: [
      "01",
      "02",
      "03",
      "04",
      "05",
      "06",
      "07",
      "08",
      "09",
      "10",
      "11",
      "12"
    ],
    years: ["2019", "2020", "2021"]
  }),
  mounted() {
    httpService.get(`${Config.apiRoot}/transactions/payers`, (status, data) => {
      // reorder so medicare and medicaid are first
      const reorderItems = ["MEDICARE", "MEDICAID"];
      reorderItems.forEach(el => {
        let indx = data
          .map(function(e) {
            return e.original_payor;
          })
          .indexOf(el);

        if (indx > -1) {
          data.splice(indx, 1);
        }
        data.unshift(el);
      });

      this.payerListing = [...data];
    });
  },
  watch: {
    selectedPractice: function(val) {
      this.getAudits(val);
      this.selectedPracticeAuditId = null;
    },
    selectedNpi: function(val) {
      this.getPhysicianAudits(val);
      this.selectedPhysicianAuditId = null;
    }
  },
  computed: {
    entities() {
      return this.$store.getters.ENTITY;
    },
    practices() {
      return this.$store.getters.PRACTICE;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN.filter(
        p => p.physician_practice_id === this.selectedPractice
      );
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    distinctProcedures() {
      return [...new Set(this.procedureListing.map(obj => obj.cpt))];
    },
    filteredPractices() {
      return this.practices.filter(
        obj => obj.entity_id === this.selectedEntityId
      );
    }
  },
  methods: {
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 5000, true);
    },
    startPracticeAudit: function() {
      console.log("Creating practice audit");
      httpService.post(
        `${Config.apiRoot}/audit/practice/audit`,
        {
          practice: this.selectedPractice,
          auditDate: this.auditMonth.concat(this.auditYear),
          allProviders: this.allProviders
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage(
              "error",
              "There was a problem creating the practice audit records - " +
                data.message.originalError.info.message
            );
            return;
          }
          this.showMessage("success", "Practice Audit records created.");
          //          this.selectedPractice = null;

          this.getAudits();
        }
      );
    },
    startPhysicianAudit: function() {
      httpService.post(
        `${Config.apiRoot}/audit/physician/audit`,
        {
          physician: this.selectedNpi,
          audit_practice_id: this.selectedPracticeAuditId
        },
        (status, data) => {
          if (!data.success) {
            this.showMessage(
              "error",
              "There was a problem creating the practice audit records - " +
                data.message.originalError.info.message
            );
            return;
          }
          this.showMessage("success", "Physician Audit record created.");
          //          this.selectedPractice = null;

          this.getPhysicianAudits();
        }
      );
    },
    getAudits: function(val = this.selectedPractice) {
      console.log("getting audit");
      httpService.get(
        `${Config.apiRoot}/audit/practice/audit/${val}`,
        (status, data) => {
          if (!data.success) {
            this.showMessage(
              "error",
              "There was a problem retrieving practice audit records"
            );
            return;
          }
          this.practiceAudits = data.message;
        }
      );
    },
    getPhysicianAudits: function(val = this.selectedNpi) {
      console.log("getting audit");
      httpService.get(
        `${Config.apiRoot}/audit/physician/audit/${val}`,
        (status, data) => {
          if (!data.success) {
            this.showMessage(
              "error",
              "There was a problem retrieving practice audit records"
            );
            return;
          }
          this.physicianAudits = data.message;
        }
      );
    },
    async searchProcedures() {
      this.procedureListing = [];
      this.selectedEncounters = [];

      const qs = `${this.selectedNpi}/${this.auditBeginDate}/${
        this.auditEndDate
      }/${this.auditPayer}`;
      httpService.get(
        `${Config.apiRoot}/transactions/${qs}`,
        (status, data) => {
          if (data) {
            this.procedureListing = data;
          }
          this.initialSearch = true; // show no data only after the first search is done
        }
      );
    },
    patientCptList(pt) {
      // pt - patient encounter
      // return array of CPT codes for a specific patient
      return [...new Set(this.patientRecords(pt).map(obj => obj.cpt))];
    },
    patientRecords(pt) {
      // pt - patient encounter
      // return array of rows for a specific patient
      return this.procedureListing.filter(obj => obj.encounter === pt);
    },
    listGroupCountForProcedures(proc) {
      return this.procedureListing.filter(obj => obj.cpt === proc).length;
    },
    addPatientToList(enc) {
      // Add encounter to list to create audits
      if (!this.selectedEncounters.includes(enc)) {
        this.selectedEncounters.push(enc);

        // hide entry on the selection list
        const indx = this.procedureListing.map(el => el.encounter).indexOf(enc);
        this.procedureListing[indx].visible = false;
      }
    },
    popPatientFromList(enc) {
      // Remove encounter from the list of audits
      this.selectedEncounters.splice(this.selectedEncounters.indexOf(enc), 1);

      // show entry back on the selection list
      const indx = this.procedureListing.map(el => el.encounter).indexOf(enc);
      this.procedureListing[indx].visible = true;
    },
    async createAudits() {
      // create audit records in database
      console.log("creating audit records");
      // WADE- not used? const qs = `?enc=${this.selectedEncounters}`;
      //      return new Promise((resolve, reject) => {
      httpService.post(
        `${Config.apiRoot}/transactions/createAudits`,
        {
          physicianAuditId: this.selectedPhysicianAuditId,
          encounters: this.selectedEncounters
        },
        (status, data) => {
          if (!data.status) {
            this.showMessage(
              "error",
              "there was a problem creating the audit records"
            );
          }
          this.showMessage(
            "success",
            "Audit records created.  TODO - create detailed message"
          );
          this.selectedEncounters = [];
        }
      );
      //     });
    },
    patientFromEncounter(enc) {
      return this.procedureListing.find(elem => elem.encounter === enc);
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.record-selector .v-list__tile {
  height: auto;
}
</style>
