<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-card class="mb-2">
        <v-card-title class="headline font-weight-regular blue-grey white--text">Physician Education</v-card-title>
        <v-card-text>
          <v-subheader class="pa-0">Physician Lookup</v-subheader>
          <v-autocomplete
            v-model="selectedNpi"
            :items="physicianLookup"
            persistent-hint
            item-text="name"
            item-value="npi"
            @change="selectPhysician(selectedNpi)"
          ></v-autocomplete>
        </v-card-text>
      </v-card>
      <v-card v-if="showPhysicianProfile && !showEducationDialog">
        <v-card-title class="headline font-weight-regular blue-grey white--text">
          Education
          <i
            class="material-icons"
            @click="showEducation()"
            style="padding-left:8px;"
          >add_circle_outline</i>
        </v-card-title>
        <v-list>
          <div v-for="type in this.distinctEducationTypes" :key="type">
            <v-subheader insert>{{ type }}</v-subheader>
            <v-list-tile
              v-if="row.type === type"
              v-for="row in physicianEducation"
              :key="row.id"
              avatar
              @click="showEducation(row.id)"
            >
              <v-list-tile-content>
                <v-list-tile-title>{{ row.education_date }} - {{ row.method }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </div>
        </v-list>
      </v-card>
      <education-dialog
        v-if="showEducationDialog"
        :education="educationDialogDetails"
        @interfaceSave="saveEducation"
        @interfaceCancel="cancelEducation"
        @interfaceDelete="deleteEducation"
      ></education-dialog>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import EducationDialog from "@/components/EducationDialog";
import CustomSnackbar from "@/components/Snackbar";
import httpService from "../httpService";

export default {
  name: "Main",
  components: {
    EducationDialog,
    CustomSnackbar
  },
  data: () => ({
    menuDatePicker: null,
    selectedNpi: null,
    showPhysicianProfile: false,
    currentPhysician: null,
    physicianEducation: null,
    showEducationDialog: false,
    educationDialogDetails: {}
  }),
  mounted() {
    //   this.currentPhysician = this.changer("");
  },
  computed: {
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    distinctEducationTypes() {
      return [...new Set(this.physicianEducation.map(obj => obj.type))];
    }
  },
  methods: {
    selectPhysician: async function(sel) {
      this.currentPhysician = Object.assign(
        {},
        this.physicians.find(p => p.npi === sel)
      );

      // fetch education records for npi
      httpService.get(
        `${Config.apiRoot}/physician/education/${this.currentPhysician.npi}`,
        (status, data) => {
          this.physicianEducation = data;

          this.showEducationDialog = false;
          this.showPhysicianProfile = true;
        }
      );
    },
    showEducation: function(id) {
      // new dialog(?) to add a new record
      if (id) {
        // existing record
        this.educationDialogDetails = Object.assign(
          {},
          this.physicianEducation.find(p => p.id === id)
        );
      } else {
        // new record
        this.educationDialogDetails = initialEducation();
        this.educationDialogDetails.npi = this.selectedNpi;
        this.educationDialogDetails.auditor_id = this.currentUser.id;
        this.educationDialogDetails.education_date = new Date()
          .toISOString()
          .split("T")[0];
      }
      this.educationDialogDetails.physician_name =
        this.currentPhysician.last_name +
        ", " +
        this.currentPhysician.first_name;
      this.educationDialogDetails.auditor_name = this.currentUser.unique_name;
      this.showEducationDialog = true;
    },
    saveEducation: async function() {
      // save record here.  if id assigned, don't update???  WADE check
      if (!this.educationDialogDetails.id) {
        httpService.post(
          `${Config.apiRoot}/physician/education`,
          this.educationDialogDetails,
          (status, data) => {
            if (data.success && data.rowsAffected == 1) {
              // only hide if successfull
              this.educationDialogDetails.id = data.new_id; // add new id first
              this.physicianEducation.push(this.educationDialogDetails);
              this.showMessage("success", "Record sucessfully saved");
            } else {
              this.showMessage(
                "error",
                "There was a problem saving your changes.  MSG - " +
                  data.message.originalError.info.message
              );
            }
          }
        );
      } else {
        this.showMessage("error", "do we update education once it's saved?");
        //        this.showEducationDialog = false;
      }
    },
    cancelEducation: function() {
      this.showEducationDialog = false;
    },
    showMessage: function(type, message) {
      this.$refs.customSnackbar.showSnackbar(message, type, 3000, false);
    }
  }
};

const initialEducation = () => {
  return {
    id: null,
    type: null,
    method: null,
    education_date: null,
    start_date: null,
    npi: null,
    auditor_id: null,
    emservices: null,
    emservices_chief_complaint: null,
    emservices_hpi: null,
    emservices_ros: null,
    emservices_pfsh: null,
    emservices_pe: null,
    emservices_1995: null,
    emservices_1997: null,
    emservices_mdm: null,
    emservices_counseling: null,
    emservices_pptx_provided: null,
    emservices_cards_provided: null,
    emservices_other1: null,
    emservices_other2: null,
    emservices_other3: null,
    emservices_other1_text: null,
    emservices_other2_text: null,
    emservices_other3_text: null,
    emservices_advanced_care_planning: null,
    emservices_chronic_care_management: null,
    emservices_medicare_wellness_visit: null,
    emservices_new_established_visits: null,
    emservices_observation_care: null,
    emservices_critical_care: null,
    emservices_home_visits: null,
    emservices_medicare_initial_exam: null,
    emservices_nursing_facility: null,
    emservices_transitional_management: null,
    emservices_other_services: null,
    emservices_other_services_text: null,
    incidentto: null,
    incidentto_direct_supervision: null,
    incidentto_employed: null,
    incidentto_patients_problems: null,
    sharedservices: null,
    mlp_direct_billing: null,
    policies: null,
    policies_addendums: null,
    policies_review_policy: null,
    policies_copy_paste: null,
    policies_scribe_services: null,
    policies_other: null,
    policies_other_text: null,
    coding_guidelines: null,
    surgical_rules: null,
    surgical_rules_fact_sheet: null,
    modifiers: null,
    modifiers_24: null,
    modifiers_25: null,
    modifiers_50_rt_lt: null,
    modifiers_57: null,
    modifiers_59: null,
    modifiers_62: null,
    modifiers_80_81_82: null,
    modifiers_as: null,
    modifiers_a1: null,
    modifiers_other: null,
    modifiers_other_text: null,
    teaching_rules: null,
    ticket_review: null,
    consulations: null,
    questions: null,
    questions_text: null
  };
};
</script>

<style>
i:hover {
  cursor: pointer;
}
