<template>
  <div>
    <v-container>
      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title
              class="headline font-weight-regular blue-grey white--text"
            >Audit Assignments</v-card-title>
            <v-card-text>
              <v-list>
                <v-list-tile-title>
                  <v-layout>
                    <v-flex md1>Status</v-flex>
                    <v-flex md2>Provider</v-flex>
                    <v-flex md2>Practice</v-flex>
                    <v-flex md1>Audit Date</v-flex>
                    <v-flex md1>Entity</v-flex>
                    <v-flex md1>Med System</v-flex>
                    <v-flex md1>Billing System</v-flex>
                    <v-flex md1>Audit Type</v-flex>
                    <v-flex md1>In Progress</v-flex>
                    <v-flex md1>Completed</v-flex>
                  </v-layout>
                </v-list-tile-title>
                <template v-for="(item, index) in audits">
                  <v-list-tile
                    :key="index"
                    @click="auditPhysician(item.npi, item.provider_audit_id)"
                  >
                    <v-layout>
                      <v-flex md1>?</v-flex>
                      <v-flex md2>{{ item.provider_name }}</v-flex>
                      <v-flex md2>{{ item.group_practice }}</v-flex>
                      <v-flex md1>{{ item.audit_date }}</v-flex>
                      <v-flex md1>{{ item.entity_name }}</v-flex>
                      <v-flex md1>{{ item.med_system }}</v-flex>
                      <v-flex md1>{{ item.billing_system }}</v-flex>
                      <v-flex md1>{{ item.audit_type }}</v-flex>
                      <v-flex md1>?</v-flex>
                      <v-flex md1>?</v-flex>
                    </v-layout>
                  </v-list-tile>
                </template>
              </v-list>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import httpService from "../httpService";

export default {
  name: "Main",
  components: {},
  data: () => ({
    audits: []
  }),
  async mounted() {
    //this.$store.dispatch("FETCH_AUDITS", { auditor: this.currentUser.id });
    httpService.get(
      `${Config.apiRoot}/audit/dashboard/${this.currentUser.id}`,
      (status, data) => {
        // sort by patient for display on list.  speed?
        this.audits = data.message;
        // this.audits = data.sort((a, b) => {
        //   const nameA = a.patient_name.toUpperCase();
        //   const nameB = b.patient_name.toUpperCase();
        //   if (nameA < nameB) {
        //     return -1;
        //   }
        //   if (nameA > nameB) {
        //     return 1;
        //   }
        //   // names must be equal
        //   return 0;
        // });
        // this.audits;
      }
    );
  },
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    },
    distinctFacilities() {
      return [...new Set(this.audits.map(obj => obj.facility))];
    }
  },
  methods: {
    auditPhysician(npi, provider_audit_id) {
      // user selected a record to work on
      Router.push({
        name: "audit-provider",
        params: { npi: npi, provider_audit_id: provider_audit_id }
      });
    },
    auditPatient(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    },
    distinctPracticesPerFacility(facility) {
      // distinct practices for the 2nd grouping of the list
      return [
        ...new Set(
          this.audits
            .filter(obj => obj.facility === facility)
            .map(obj => obj.group_practice)
        )
      ];
    },
    distinctPracticeAuditsPerFacility(facility, practice) {
      // distinct audit dates for practices for the 3rd grouping of the list
      return [
        ...new Set(
          this.audits
            .filter(
              obj =>
                obj.facility === facility && obj.group_practice === practice
            )
            .map(obj => obj.practice_created_date)
        )
      ];
    },
    distinctPhysiciansPerPractice(facility, practice, created_date) {
      // distinct physicians for the 4th grouping of the list
      return [
        ...new Set(
          this.audits
            .filter(
              obj =>
                obj.facility === facility &&
                obj.group_practice === practice &&
                obj.practice_created_date === created_date
            )
            .map(obj => obj.physician_name)
        )
      ];
    }
  }
};
</script>

<style>
i:hover {
  cursor: pointer;
}

.indent-1 {
  margin-left: 15px;
}

.indent-2 {
  margin-left: 30px;
}

.indent-3 {
  margin-left: 45px;
}

.indent-4 {
  margin-left: 60px;
}
</style>
